  const [talkToUsShow, setTalkToUsShow] = useState(false);

  const faqData = [
    {
      question: "What is a cryptocurrency exchange?",
      answer:
        "A cryptocurrency exchange is a platform that allows users to buy, sell, and trade digital currencies. It acts as an intermediary between buyers and sellers, facilitating transactions in various cryptocurrencies.",
    },
    {
      question: "How does cryptocurrency trading work?",
      answer:
        "Cryptocurrency trading involves exchanging one cryptocurrency for another or converting it to fiat currency. Traders can speculate on price movements to profit, utilizing various trading pairs offered on the exchange.",
    },
    {
      question: "What are the different types of cryptocurrency exchanges?",
      answer:
        "There are three main types of cryptocurrency exchanges: centralized exchanges (CEX), decentralized exchanges (DEX), and peer-to-peer (P2P) platforms. Each type has unique features and benefits depending on user needs.",
    },
    {
      question:
        "What are the fees associated with using a cryptocurrency exchange?",
      answer:
        "Fees can vary significantly between exchanges, including trading fees, withdrawal fees, and deposit fees. Its essential to review the fee structure of each exchange before trading to understand potential costs.",
    },
    {
      question: "How secure is cryptocurrency trading?",
      answer:
        "While cryptocurrency exchanges implement various security measures, no exchange is entirely risk-free. Users should utilize two-factor authentication (2FA) and consider using hardware wallets for added security",
    },
    {
      question: "Can I trade cryptocurrencies on mobile devices?",
      answer:
        "Yes, most cryptocurrency exchanges offer mobile applications, allowing users to trade and manage their assets on the go. These apps provide features similar to their desktop counterparts for a seamless trading experience.",
    },
    {
      question: "How can I choose the right cryptocurrency exchange?",
      answer:
        "Factors to consider include security features, user interface, trading fees, available cryptocurrencies, customer support, and regulatory compliance. Researching these aspects will help you select the most suitable exchange for your needs.",
    },
    {
      question: "What should I do if I forget my exchange password?",
      answer:
        "Most exchanges offer a password recovery option through email verification or security questions. It's important to follow the provided steps carefully to regain access to your account.",
    },
    {
      question: "What features should I look for in a cryptocurrency exchange?",
      answer:
        "Look for essential features such as an intuitive user interface, advanced trading tools, a wide selection of cryptocurrencies, robust security measures, and reliable customer support to ensure a smooth trading experience.",
    },
    {
      question:
        "How can I ensure regulatory compliance when starting my own cryptocurrency exchange?",
      answer:
        "Research and understand the legal requirements in your jurisdiction, including licensing and KYC/AML regulations. Consulting with legal experts in cryptocurrency regulations can help ensure your exchange operates within the law.",
    },
  ];
  // const onPrevClick = () => {
  //   ref.current.prev();
  // };
  // const onNextClick = () => {
  //   ref.current.next();
  // };

