import React from "react";
import { graphql } from "gatsby";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";
import Layout from "@components/layout";
import Seo from "@components/seo";
import BreadCrumb from "@components/common/breadcrumb";
import CryptoExchangeScreen from "@screens/crypto-exchange";

const CryptoDevelopment = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);
  const breadcrumbLinks = [
    { text: "Home", link: "/" },
    { text: "Cryptocurrency Exchange Development Company", link: null },
  ];
  return (
    <Layout db={db}>
      <BreadCrumb items={breadcrumbLinks} />
      <main>
        <CryptoExchangeScreen db={db} />
      </main>
    </Layout>
  );
};

export const Head = (props) => {
  const { data } = props;
  const fileUrl =
  data?.cryptoExchangeDevelopmentImg?.childImageSharp?.gatsbyImageData?.images?.fallback
    ?.src;
  return (
    <Seo
      title="Cryptocurrency Exchange Development Company"
      metaTitle="Cryptocurrency Exchange Development Company"
      description="justtrytech.com is a leading crypto exchange development company in India. They provide excellent  customized crypto exchange softwares with the industry's best crypto exchange developers."
      image={fileUrl}
    ></Seo>
  );
};

export default CryptoDevelopment;

export const queryData = graphql`
  query MyQuery {
   cryptoExchangeDevelopmentImg: file(relativePath: { eq: "crypto-exchange-development.png" }) {
      childImageSharp {
        gatsbyImageData
      }
    }
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
      
      }
`;
